.item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 45px;
  margin: 0;
  margin-bottom: 3px;
  padding: 5px;
  border-radius: 3px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.name {
  font-size: 13px;
  margin: 0px;
  width: 180px;
}

.dimension {
  font-size: 13px;
  margin: 0px;
  width: 200px;
}

.type {
  font-size: 13px;
  margin: 0px;
  width: 90px;
}
